









































import {Component, Vue, Watch} from "vue-property-decorator"
import {BookingGender, IBookingAvailability, IPayment, TrainingSessionDuration, TrainingSessionType} from "@chaosinsight/yogipeople-portalclient"
import Portal from "@/managers/portal"
import Profile from "@/managers/profile"
import RequestRadio from "@/components/RequestRadio.vue"
import CardPayment from "@/components/CardPayment.vue"
import i18n from "../i18n"
import { TranslateResult } from "vue-i18n"

@Component({ components: { CardPayment, RequestRadio } })
export default class TrainLive extends Vue {
	public durations: TrainingSessionDuration[] = [30, 60]
	public types: TrainingSessionType[] = [TrainingSessionType.vinyasa, TrainingSessionType.yin, TrainingSessionType.hatha, TrainingSessionType.ashtanga, TrainingSessionType.freeform]
	public genders: BookingGender[] = [BookingGender.any, BookingGender.male, BookingGender.female]

	public requestDuration: TrainingSessionDuration = this.durations[1]
	public requestType: TrainingSessionType = this.types[2]
	public requestGender: BookingGender = this.genders[0]

	private slide: number = 1

	private daysInWeek: TranslateResult[] = [i18n.t("sunday"), i18n.t("monday"),
		i18n.t("tuesday"), i18n.t("wednesday"), i18n.t("thursday"),
		i18n.t("friday"), i18n.t("saturday")]
	private days: TranslateResult[] = []
	private hours: IBookingAvailability[] = []
	private availableDates: number[] = []
	private booking: IBookingAvailability | null = null

	private systemTime: string = ""

	/**
	 * Get system time
	 */

	private getSystemTime(): Promise<string> {
		return Portal.client.call.system.Time()
			.response
			.then(res => res.Time)
	}

	private get isEmailVerified(): boolean | null {
		return Profile.isReady
			? Profile.profile!.IsMobileVerified
			: null
	}

	private sendVerificationEmail(): void {
		Portal.client.call.account.requestEmailAddressVerification()
			.response
			.then(() => {
				this.$emit("modalEmailVerify", "A verification link has been sent to your email address - please click the link")
			})
	}

	/**
	 * Set booking termin
	 */

	private setBooking(booking: IBookingAvailability): void {
		this.booking = booking
		this.slide = 3

	}

	private showDefaultPanel(): void {
		this.slide = 1
		this.$emit("close")
	}

	/**
	 * Booking Availability
	 */

	private bookingAvailability(): void {

		this.getSystemTime()
			.then(time => {
				this.systemTime = time
				const fromDate = new Date(time)
				const toDate = new Date(time)
				toDate.setUTCDate(fromDate.getUTCDate() + 7)

				Portal.client.call.booking.bookingAvailability(fromDate, toDate, this.requestDuration, this.requestType, this.requestGender, true)
					.response
					.then(result => {
						this.populateDaysHours(result)
						this.slide = 2
					})
					.catch(reason => {
						throw reason
					})
			})

	}

	/**
	 * Populate days and hours arrays
	 */

	private populateDaysHours(slots: IBookingAvailability[]) {
		this.hours = slots
		slots.forEach(el => {
			const day = new Date(el.TimeSlot.Start).getDate()
			if (this.availableDates.indexOf(day) === -1) {
				this.availableDates.push(day)
				this.days.push(el.TimeSlot.Start)
			}
		})
	}

	/**
	 * @returns day label i.e. Today, Tomorrow, Friday...
	 */

	private getDayLabel(day: string): string {
		const date = new Date(day)
		const today = new Date()
		const tomorrow = new Date()
		tomorrow.setDate(today.getDate() + 1)

		if (date.getDate() === today.getDate())
			return i18n.t("today").toString()
		else if (date.getDate() === tomorrow.getDate())
			return i18n.t("tomorrow").toString()
		return `${this.daysInWeek[new Date(day).getDay()]} ${new Date(day).getMonth() + 1}/${new Date(day).getDate()}`
	}

	/**
	 * Check if hour exists for particular day
	 */

	private checkDay(day: string, hour: IBookingAvailability): boolean {
		const d1 = new Date(day)
		const d2 = new Date(hour.TimeSlot.Start)

		if (d1.getDay() === new Date(this.systemTime).getDay()) {
			const minOffset = new Date(this.systemTime)
			minOffset.setMinutes(minOffset.getMinutes() + 10)
			return d1.getDate() === d2.getDate() && d2 > minOffset
		}
		return d1.getDate() === d2.getDate()
	}

	/**
	 * Check is day has slots
	 */
	private isDayHasSlots(day: string): boolean {
		const date = new Date(day).getDate()
		const hasSlots = this.hours.filter(el => date === new Date(el.TimeSlot.Start).getDate())
		return !!hasSlots.length
	}

	/**
	 * returns i.e. 10am or 1:30pm
	 */

	private setHour(hour: IBookingAvailability): string {
		return this.amPm(new Date(hour.TimeSlot.Start).getHours(), new Date(hour.TimeSlot.Start).getMinutes())
	}

	/**
	 * Convert hour in am/pm format
	 */

	private amPm(hour: number, min: number): string {
		const m = (min && min < 10) ? this.leadZero(min) : min
		if (hour > 12)
			return `${hour % 12}${m ? ":" + m : ""}pm`
		else if (hour < 12)
			return `${hour}${m ? ":" + m : ""}am`
		return `12${m ? ":" + m : ""}pm`
	}

	/**
	 * Lead zero
	 */

	private leadZero = (x: number): string => x < 10 ? x < 0 ? `00` :	`0${x}` : `${x}`

	/**
	 * Booking POST
	 */

	private postBooking(): void {
		const booking: IBookingAvailability = this.booking as IBookingAvailability
		const startDescription = this.getTimeDescription(new Date(booking.TimeSlot.Start))

		Portal.client.call.booking.any(booking.Id, this.requestType, this.requestDuration, this.requestGender, true)
			.response
			.then(() => {
				this.slide = 1
				this.$emit("modal", this.requestDuration, this.requestType, this.formatTermin(booking))
			})
			.catch(reason => {
				this.slide = 1
				this.$emit("modalErr", reason)
				throw reason
			})
	}

	// format termin string for modal box
	private formatTermin(booking: IBookingAvailability): string {
		const hour = this.setHour(booking)
		let day = this.getDayLabel(booking.TimeSlot.Start)
		if ([i18n.t("home.student.trainLive.today"), i18n.t("home.student.trainLive.tomorrow")].indexOf(day.toLowerCase()) !== -1)
			day = day.toLowerCase()
		return `${day} at ${hour}`
	}

	/**
	 * Get time description
	 */

	private getTimeDescription(value: Date): TranslateResult {
		const now = new Date(new Date().toUTCString())
		const nowTime = now.getTime()
		const startTime = value.getTime()

		if (startTime < nowTime)
			return i18n.t("home.student.trainLive.past")

		if (startTime - nowTime < 3600000)
			return i18n.t("home.student.trainLive.now")

		if (startTime - nowTime < 3600000 * 24 && now.getDay() === value.getDay())
			return i18n.t("home.student.trainLive.today")

		if (startTime - nowTime < 3600000 * 48 && now.getDay() + 1 === value.getDay())
			return i18n.t("home.student.trainLive.tomorrow")

		return i18n.t("home.student.trainLive.future")
	}
}
